<template>
  <div>
    <el-container>
      <el-aside width="232px" style="height: 100vh">
        <p
          style="
            font-weight: 600;
            font-size: 17px;
            margin-top: 12px;
            width: 200px;
            margin-left: -12px;
            margin-bottom: 8px;
          "
        >
          超级企业列表({{ num }})
        </p>
        <!-- 输入框 -->
        <el-input
          v-model="monitr"
          style="width: 135px; height: 50px; margin-right: 9px"
          placeholder="请输入内容"
          @input="searchfun()"
        ></el-input>
        <!-- 添加按钮 -->
        <button class="btn" @click="dialogFormVisible = true">
          <i class="el-icon-plus" style="font-size: 18px"></i>
        </button>
        <el-dialog title="收货地址" :visible.sync="dialogFormVisible">
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="150px"
            class="demo-ruleForm"
          >
            <el-form-item label="超级企业名称" prop="ename">
              <el-input v-model="ruleForm.ename"></el-input>
            </el-form-item>
            <br />
            <el-form-item label="ip域名" prop="ipstr">
              <el-input v-model="ruleForm.ipstr"></el-input>
            </el-form-item>
            <br />
            <el-form-item label="超级管理员账号" prop="adminname">
              <el-input v-model="ruleForm.adminname"></el-input>
            </el-form-item>
            <br />
            <el-form-item label="邮箱" prop="adminemil">
              <el-input v-model="ruleForm.adminemil"></el-input>
            </el-form-item>
            <br />
            <el-form-item label="最大坐席数" prop="maxnum">
              <el-input v-model="ruleForm.maxnum"></el-input>
            </el-form-item>
            <br />
            <el-form-item label="最大登录并发总数" prop="maxconcurrency">
              <el-input v-model="ruleForm.maxconcurrency"></el-input>
            </el-form-item>
            <br />
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm')"
                >立即创建</el-button
              >
              <el-button @click="resetForm('ruleForm')">取消创建</el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
        <ul>
          <li v-for="(item, index) in arr" :key="index" @click="details(item)">
            <div :class="item.status ? 'green' : 'red'"></div>
            {{ item.ename }}
          </li>
        </ul>
      </el-aside>
      <el-container>
        <el-main style="background-color: white">
          <div style="margin-bottom: 10px; width: 280px; margin-left: 10px">
            <el-radio-group v-model="tabPosition">
              <router-link to="/first/business/super">
                <el-button
                  label="top"
                  type="primary"
                  plain
                  class="buttoncla"
                  style="
                    border-top-right-radius: 0px;
                    border-bottom-right-radius: 0px;
                  "
                >
                  超级企业管理
                </el-button>
              </router-link>
              <router-link to="/first/business/son" class="tab_data">
                <el-button
                  label="top"
                  type="primary"
                  plain
                  class="buttoncla"
                  style="
                    border-top-left-radius: 0px;
                    border-bottom-left-radius: 0px;
                  "
                >
                  子企业管理
                </el-button>
              </router-link>
            </el-radio-group>
          </div>
          <router-view @a="pp()" />
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>
<script>
import { getenter, getsearch, getdetails, getadddata } from "../../api/https";
import { mapState } from "vuex";
export default {
  data() {
    return {
      tabPosition: "left",
      monitr: "",
      arr: [],
      num: 0,
      dialogFormVisible: false,
      ruleForm: {
        ename: "",
        ipstr: "",
        adminname: "",
        adminemil: "",
        maxnum: "",
        maxconcurrency: "",
      },
      rules: {
        ename: [
          { required: true, message: "请输入超级企业名称", trigger: "blur" },
        ],
        ipstr: [{ required: true, message: "请输入ip域名", trigger: "blur" }],
        adminname: [
          { required: true, message: "请输入超级管理员账号", trigger: "blur" },
        ],
        adminemil: [{ required: true, message: "请输入邮箱", trigger: "blur" }],
      },
    };
  },
  computed: {
    ...mapState({
      ArrData: (state) => state.arr,
    }),
  },
  methods: {
    pp() {
      this.fun();
    },
    // 添加
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let obj = {
            ename: this.ruleForm.ename,
            ipstr: this.ruleForm.ipstr,
            adminname: this.ruleForm.adminname,
            adminemil: this.ruleForm.adminemil,
            maxnum: this.ruleForm.maxnum,
            maxconcurrency: this.ruleForm.maxconcurrency,
          };
          getadddata(obj).then((res) => {
            console.log(res);
            this.fun();
            this.dialogFormVisible = false;
            this.$message("添加成功");
          });
        } else {
          this.$message("已取消");
          return false;
        }
      });
    },
    resetForm() {
      this.dialogFormVisible = false;
    },
    // 详情
    details(item) {
      getdetails({ id: item.id }).then((res) => {
        this.$store.dispatch("upnum", res.data);
        console.log(this.$store.state.arr, 666666666);
      });
    },
    // 数据渲染
    fun() {
      getenter().then((res) => {
        this.num = this.arr.length;
        this.arr = res.data.data;
      });
    },
    // 模糊搜索
    searchfun() {
      let obj = { ename: this.monitr };
      getsearch(obj).then((res) => {
        this.arr = res.data.data;
        this.num = res.data.data.length;
      });
    },
  },
  created() {
    this.fun();
    this.details({ id: 69 });
  },
};
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
}
.buttoncla {
  width: 130px;
  height: 36px;
  border: none;
  border-radius: 6px;
  font-size: 14px;
  color: #606266;
  border-bottom: 1px solid #dcdfe6;
  border-top: 1px solid #dcdfe6;
  border-right: 1px solid #dcdfe6;
  margin-top: 6px;
}

.green {
  display: inline-block;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  border: none;
  background-color: #38d88f;
  margin-right: 5px;
  margin-bottom: 3px;
}
.red {
  display: inline-block;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  border: none;
  background-color: #ff000d;
  margin-right: 5px;
  margin-bottom: 3px;
}

li {
  list-style: none;
  border-bottom: 1px solid #f3f3f5;
  height: 40px;
  text-align: left;
  line-height: 40px;
  color: #9dadca;
  padding-left: 14px;
}

li:hover {
  background-color: #ecf5ff;
}

.btn {
  width: 40px;
  height: 40px;
  border: none;
  background-color: #eaf6fe;
  color: #34a5fe;
}

.el-header,
.el-footer {
  background-color: #b3c0d1;
  color: #333;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  background-color: #ffffff;
  color: #333;
  text-align: center;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
  line-height: 62px;
}

body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
</style>
